.login-container{

    width: 25%;
    height: 50%;

    display: flex;
    margin: auto;

    margin-top: 2%;

    border-radius: 10px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;

    background: #0062cc;

    
    text-align: center;
    
    align-items: center;
    
    justify-content: center;
    
    input {
        text-decoration: none;
        margin-bottom: 30px;
        border-radius: 2%;
    }

    h3 {
        font-size: 22px;
        color: #fff;
        margin-bottom: 10%;
    }

    .form-control {
        font-size: 16px;
        outline: none;
        text-decoration: none;
        border-radius: 5px;
        border-color: #fff;
        padding: 2%;
        
    }

    .btnSubmit {
        background-color: #fff;
        width: 50%;
        border-radius: 1rem;
        padding: 3%;
        border: none;
        cursor: pointer;

        &:hover {
            background-color:darken($color: #fff, $amount: 10);
        }
    }
 
    .Link {
        color: #fff;
        &:hover {
            color:darken($color: #fff, $amount: 10);
        }
    }

    
    
    
     
}

.google-button {
    height: 30px;
    border-width: 0;
    background: white;
    color: #737373;
    border-radius: 5px;
    white-space: nowrap;
    box-shadow: 1px 1px 0px 1px rgba(0,0,0,0.1);
    transition-property: background-color, box-shadow;
    transition-duration: 150ms;
    transition-timing-function: ease-in-out;
    padding: 0;
    transform: scale(1.4);
}
.google-button:hover, .google-button:focus{
    box-shadow: 1px 4px 5px 1px rgba(0,0,0,0.2);
    outline: none;
    transform: scale(1.4) skewX(-0.4deg);
}
.google-button:active{
    outline: none;
    box-shadow: 1px 4px 5px 1px rgba(0,0,0,0.3);
    transition-duration: 10ms;
}
.google-button__icon {
    display: inline-block;
    vertical-align: middle;
    margin: 0px 0 8px 8px;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
}
.google-button__icon--plus {
    width: 27px;
}
.google-button__text {
    display: inline-block;
    vertical-align: middle;
    padding: 0 24px;
    font-size: 14px;
    font-weight: bold;
    font-family: 'Roboto',arial,sans-serif;


}


@media screen and ( max-width: 921px ) {

    .login-container {
        width: 40%;
    }

    
}

@media screen and ( max-width: 722px ) {

    .login-container {
        align-items: flex-start;
        margin-top: 0;
        width: 90%;
        height: 100vh;
        border-radius: 0;
        border: none;



        h3 {
            margin-top: 30%;
        }

        .form-control {
            padding: 4%;
        }


        .btnSubmit {

            font-size: 16px;
            padding: 4%;
           
        }
        
    }

    
}














