
.navbar__container {

    position: relative;

    display: flex;

    align-items: center;

    padding: 0;
    margin: 0;

    background-color: #1a4a5a;   
    width: 100vw;
    height: 10vh;

    .title {
        font-size: 24px;
        margin: auto;
        color: #fff;
    }

    .fa-bars{
        
        visibility: hidden;
    }

    .navbar__logout-container {
        position: absolute;
        font-size: 18px;
        display: flex;
        flex-direction: row;
        color: #fff;

        justify-content: center;
        align-items: center;

        margin-left: 3%;


        p {
            margin-left: 10%;
        }

        .name {
            padding-right: 10%;
            margin-right: 10%;
            border-right: 2px solid #fff;
            border-color: #fff;

        }

    }

}



@media screen and ( max-width: 921px ) {

    
}

@media screen and ( max-width: 722px ) {

    
    h1 {
        visibility: hidden;
    }
    
    .navbar__container {

        
        .fa-bars {
            font-size: 40px;
            color: #fff;
            position: absolute;
            right: 3%;
            visibility: visible;
        }
        
        .navbar__logout-container {
            
            margin: 0;
            padding: 0;

            justify-content: center;

            

            

            .fa-sign-out-alt {
                position: absolute;
                font-size: 30px;
            }
    
            p {
                visibility: hidden;
            }
    
        }
    }
    
}
