

.dashboard__navbar {
    color: #fff;
    background-color: #393E41;
    position: fixed;
    width: 20%;
    height: 100%;

    h1 {
        font-size: 30px;
        text-align: center;
        margin-top: 5%;
        padding-bottom: 5%;
        margin-bottom: 20%;
        border-bottom: 1px solid #fff;
    }

    .dashboard__navbar-links {
        text-decoration: none;
        color: #fff;
        display: block;
        font-size: 25px;
        text-align: center;
        padding-top: 7%;
        padding-bottom: 7%;

        &:hover {
            background-color: darken($color: #393E41, $amount: 5);
        }
        
    }
    .active {
        background-color: darken($color: #393E41, $amount: 8);
        &:hover {
            background-color: darken($color: #393E41, $amount: 8);
        }
    }

    .btnSubmit {
        background-color: #fff;
        width: 50%;
        border-radius: 1rem;
        padding: 3%;
        border: none;
        cursor: pointer;

        &:hover {
            background-color:darken($color: #fff, $amount: 10);
        }
    }
}