
.openModalButton {
    position: absolute;
    
    .AddIcon {
        font-size: 35px;
    }

    background-color: #024A86;
    color: #FFF;

    border: none;
    border-radius: 37.5px;

    left: 54%;
    bottom: 5%;

    padding: 1.25%;

    

    cursor: pointer;

}

.modal__container {
    position: absolute;

    background-color: darken($color: #024A86, $amount: 10);
    color: #FFF;

    border: none;
    border-radius: 3px;
    box-shadow: 5px 5px #000;

    width: 40%;
    height: 95%;

    margin-left: 7.5%;
    margin-top: 0%;

    z-index: 100;


    .closeModalButton {
        position: absolute;

        background-color: darken($color: #024A86, $amount: 10);

        right: 2%;
        top: 2%;

        border: none;

        .fa-window-close {

            color: #FFF;
            font-size: 20px;

            border: none;

        }
        &:hover {
            cursor: pointer;
        }
    }

    .btnSubmit {

        position: absolute;

        display: block;
        
        border: none;
        border-radius: 15px;

        background-color: #2868f2;
        color: #fff;

        bottom: 5%;
        left: 42.5%;

        padding: 2%;

        &:hover {
            transition: all .4s;
            background-color: darken($color: #2868f2, $amount: 10);
            cursor: pointer;
        }

        
        
    }
}

.input__container-1 {
    position: absolute;

    width: 2%;
    height: 2%;

    left: 10%;
    top: 10%;
}

.input__container-2 {
    position: absolute;

    width: 2%;
    height: 2%;

    left: 55%;
    top: 10%;
}

.products_input-image {
    display: none;
}

.btnImageProductCreates {

    display: block;

    border: none;
    border-radius: 3px;
    border-bottom: 3px solid #237085;

    padding: 10px 21px;

    background-color: #3991A9;
    color: #FFF;

    font-size: 14px;
    text-align: center;
    font-family: sans-serif;

    cursor: pointer;

    left: 10%;
    top: 26%;

    &:hover {
        transition: all .4s;
        background-color: #237085;
    }

}

.imagePreviewProduct {
    position: absolute;


    img{
        
        position: absolute;

        width: 100%;
        height: 100%;

    }


    left: 10%;
    top: 35%;

    width: 250px;
    height: 250px;
}

.products__new-entry-title {
    position: absolute;

    top: 1%;
    left: 2%;
}

.products_input {

    padding: 8px 10px;

    border: none;

    margin-bottom: 20px;
}

.description {
    position: absolute;

    left: 10%;
    top: 80%;

    padding: 7.5px 10px;

    width: 76%;
    height: 5%;

    border: none;

    margin-bottom: 15px;

}
.DeleteProductButton {
    position: absolute;
    
    .DeleteIcon {

        font-size: 40px;
    }

    color: red;

    border: none;
    background-color: transparent;

    left: 49%;
    bottom: 7%;


    cursor: pointer;
}

.EditProductButton {
    position: absolute;
    
    .EditIcon {

        font-size: 40px;
    }

    color: black;

    border: none;
    background-color: transparent;

    left: 61.5%;
    bottom: 7%;


    cursor: pointer;
}

